<template>
  <div class="product-page">
    <!-- Header nằm trên toàn bộ sản phẩm -->
    <header class="product-header">
      <h2>Product Categories</h2>
    </header>

    <!-- Layout với Sidebar và Product List -->
    <div class="product-layout">
      <!-- Sidebar chỉ chứa Select Category -->
      <aside class="category-sidebar">
        <div class="category-select">
          <label for="category">Select Category: </label>
          <select v-model="selectedCategory" @change="filterProducts">
            <option value="">All Categories</option>
            <option v-for="category in categories" :key="category" :value="category">{{ category }}</option>
          </select>
        </div>
      </aside>

      <!-- Phần hiển thị sản phẩm -->
      <section class="product-list-section">
        <div v-if="filteredFurniture.length === 0" class="no-products">No products found for this category.</div>
        <div v-else class="product-card" v-for="product in filteredFurniture" :key="product._id">
          <div class="card-content">
            <img :src="`http://localhost:5000/uploads/${product.image}`" alt="Product Image" class="product-image" />
            <h3 class="product-name">{{ product.name }}</h3>
            <p class="product-description">{{ product.description }}</p>
            <p class="product-price">Price: ${{ product.price }}</p>
            <p v-if="product.inStock" class="in-stock">In Stock</p>
            <p v-else class="out-of-stock">Out of Stock</p>
                  <!-- Nút Chi Tiết -->
<button class="detail-button" @click="goToDetail(product._id)">Chi Tiết</button>

          </div>
        </div>
      </section>
    </div>
  </div>
</template>

<script>
import { mapActions, mapState } from 'vuex';

export default {
  data() {
    return {
      selectedCategory: '',
      categories: ['Ghế', 'Bàn', 'Giường Ngủ', 'Salon Phòng Khách', 'Bếp ăn']
    };
  },
  computed: {
    ...mapState(['furniture', 'filteredFurniture']),
    filteredFurniture() {
      if (!this.selectedCategory) {
        return this.furniture;
      }
      return this.furniture.filter(product => product.category === this.selectedCategory);
    }
  },
  methods: {
      goToDetail(productId) {
    this.$router.push(`/product/${productId}`);
  },
    ...mapActions(['fetchFurniture', 'fetchFurnitureByCategory']),
    filterProducts() {
      if (this.selectedCategory) {
        this.fetchFurnitureByCategory(this.selectedCategory);
      } else {
        this.fetchFurniture();
      }
    }
  },
  mounted() {
    this.fetchFurniture();
  }
};
</script>

