  <div class="accordion">
    <div v-for="(item, index) in questions" :key="index" class="accordion-item" :class="{ open: item.open }">
      <div class="accordion-header" @click="toggleItem(index)">
        <h3>{{ item.question }}</h3>
        <i :class="item.open ? 'fas fa-chevron-up' : 'fas fa-chevron-down'"></i>
      </div>
