<script>
import { mapState } from 'vuex';

export default {
  data() {
    return {
      currentIndex: 0, // Vị trí hiện tại của slider
      itemsPerPage: 3, // Số sản phẩm hiển thị
         questions: [
        {
          question: "Where can I sell furniture online?",
          answer: "You can sell furniture online from your own store built with a platform like Shopify, on third-party marketplaces like Amazon and eBay, on niche websites like ArtDeco and 1dibs, and on social media, and via wholesale.",
          open: false
        },
        {
          question: "Can I sell furniture on Shopify?",
          answer: "Yes, you can sell furniture on Shopify. Onske is one example of a Shopify merchant that sells furniture.",
          open: false
        },
        {
          question: "What kind of furniture sells best?",
          answer: "Dining room/kitchen chairs, Office chairs and other home office furniture, Antique and vintage furniture, End/side tables and nightstands.",
          open: false
        },
        {
          question: "How do I price furniture?",
          answer: "To price furniture, total all of the costs associated with bringing your product to market, set your profit margin on top of those expenses, and list your prices. You can change prices over time to increase sales or profit margins.",
          open: false
        }
      ]
    };
  },
  computed: {
    ...mapState(['furniture']),
    visibleFurniture() {
      // Lấy các sản phẩm dựa trên vị trí hiện tại và số lượng sản phẩm mỗi trang
      const start = this.currentIndex;
      const end = start + this.itemsPerPage;
      return this.furniture.slice(start, end);
    }
  },
  methods: {
    nextSlide() {
      if (this.currentIndex + this.itemsPerPage < this.furniture.length) {
        this.currentIndex += this.itemsPerPage;
      }
    },
    prevSlide() {
      if (this.currentIndex > 0) {
        this.currentIndex -= this.itemsPerPage;
      }
    },
    toggleItem(index) {
    this.questions[index].open = !this.questions[index].open; // Đảo ngược trạng thái của open
  }
    
  },
  created() {
    this.$store.dispatch('fetchFurniture');
  }
};
</script>

