import store from '@/store';
import CartPage from '@/views/CartPage.vue';
import ContactPage from '@/views/ContactPage.vue';
import HomePage from '@/views/HomePage.vue';
import LoginPage from '@/views/LoginPage.vue';
import ManageFurniture from '@/views/ManageFurniture.vue';
import ProductPage from '@/views/ProductPage.vue';
import RegisterPage from '@/views/RegisterPage.vue';
import { createRouter, createWebHistory } from 'vue-router';

const routes = [
  { path: '/', component: HomePage },
  { path: '/login', component: LoginPage },
  { path: '/register', component: RegisterPage },
  { path: '/contact', component: ContactPage },
  { path: '/cart', component: CartPage },
  {
    path: '/product/:id',
    name: 'ProductDetail',
    component: () => import('@/views/ProductDetailPage.vue'),
    props: true
  },
  { path: '/product', name: 'ProductPage', component: ProductPage, props: true },
  {
    path: '/manage-furniture',
    component: ManageFurniture,
    beforeEnter: async (to, from, next) => {
      const token = localStorage.getItem('token'); // Lấy token từ localStorage
      let user = store.state.user;

      // Kiểm tra nếu chưa có user trong Vuex store, tải lại từ token
      if (!user && token) {
        await store.dispatch('loadUserFromToken'); // Tải user từ token
        user = store.state.user;
      }

      // Kiểm tra quyền truy cập
      if (user && (user.role === 'admin' || user.role === 'employee')) {
        next(); // Cho phép truy cập
      } else {
        next('/login'); // Chuyển đến login nếu không có quyền
      }
    }
  }
];

const router = createRouter({
  history: createWebHistory(),
  routes
});

export default router;
