<template>
  <div id="app">
    <!-- Hiển thị Navbar và Footer nếu không phải là trang login hoặc register -->
    <Navbar v-if="!isAuthPage" />
    <main class="main-content">
      <router-view></router-view>
    </main>
    <Footer v-if="!isAuthPage" />
  </div>
</template>

<script>
import Footer from './components/Footer.vue';
import Navbar from './components/Navbar.vue';

export default {
  name: 'App',
  components: {
    Navbar,
    Footer
  },
  computed: {
    // Kiểm tra nếu route hiện tại là trang login hoặc register
    isAuthPage() {
      return this.$route.path === '/login' || this.$route.path === '/register';
    }
  }
};
</script>

