html, body {
  height: 100%;
  margin: 0;
  padding: 0;
}

#app {
  display: flex;
  flex-direction: column;
  min-height: 100vh; /* Đảm bảo div#app chiếm toàn bộ chiều cao màn hình */
}

.app-container {
  flex: 1;
  display: flex;
  justify-content: center;
  align-items: center;
}
</style>
