import axios from "axios";
import { createStore } from "vuex";

export default createStore({
  state() {
    return {
      token: localStorage.getItem("token") || "",
      user: JSON.parse(localStorage.getItem("user") || "{}"),
      furniture: [],
      filteredFurniture: [],
      cart: JSON.parse(localStorage.getItem("cart") || "[]"), // Giỏ hàng từ Local Storage
    };
  },
  mutations: {
    SET_TOKEN(state, token) {
      state.token = token;
    },
    SET_USER(state, user) {
      state.user = user;
      localStorage.setItem("user", JSON.stringify(user));
    },
    LOGOUT(state) {
      state.token = "";
      state.user = null;
      state.cart = [];
      localStorage.removeItem("token");
      localStorage.removeItem("user");
      localStorage.removeItem("cart"); // Xóa giỏ hàng khi logout
    },
    SET_FURNITURE(state, furniture) {
      state.furniture = furniture;
    },
    SET_FILTERED_FURNITURE(state, filteredFurniture) {
      state.filteredFurniture = filteredFurniture;
    },
    ADD_TO_CART(state, product) {
      const existingProduct = state.cart.find(
        (item) => item._id === product._id
      );
      if (existingProduct) {
        existingProduct.quantity += product.quantity;
      } else {
        state.cart.push(product);
      }
      localStorage.setItem("cart", JSON.stringify(state.cart)); // Lưu giỏ hàng vào Local Storage
    },
    SET_CART(state, cart) {
      state.cart = cart;
      localStorage.setItem("cart", JSON.stringify(state.cart)); // Đồng bộ giỏ hàng vào Local Storage khi fetch từ server
    },
    REMOVE_FROM_CART(state, productId) {
      state.cart = state.cart.filter((item) => item._id !== productId);
      localStorage.setItem("cart", JSON.stringify(state.cart)); // Cập nhật lại giỏ hàng trong Local Storage
    },
  },
  actions: {
    async loadUserFromToken({ commit, dispatch, state }) {
      if (state.token) {
        try {
          const response = await axios.get(
            "http://localhost:5000/api/users/me",
            {
              headers: {
                Authorization: `Bearer ${state.token}`,
              },
            }
          );
          commit("SET_USER", response.data.user);
          await dispatch("fetchCart"); // Tải giỏ hàng từ server khi đăng nhập
        } catch (error) {
          console.error("Failed to load user:", error);
          commit("LOGOUT");
        }
      } else if (state.user.facebookId) {
        // Nếu user đăng nhập qua Facebook, vẫn commit user lên store
        await dispatch("fetchCart");
      }
    },
    async login({ commit, dispatch }, { email, password }) {
      try {
        const response = await axios.post(
          "http://localhost:5000/api/users/login",
          { email, password }
        );
        const token = response.data.accessToken;
        const user = response.data.user;

        commit("SET_TOKEN", token);
        commit("SET_USER", user);
        localStorage.setItem("token", token);
        localStorage.setItem("user", JSON.stringify(user));

        // Tải lại giỏ hàng từ server sau khi đăng nhập
        await dispatch("fetchCart");

        return response;
      } catch (error) {
        console.error("Login failed:", error);
        throw error;
      }
    },
    async facebookLogin({ commit }) {
      try {
        const response = await axios.get("http://localhost:5000/profile");
        const user = response.data.user;

        commit("SET_USER", user);
        localStorage.setItem("user", JSON.stringify(user));

        // Tải lại giỏ hàng từ server sau khi đăng nhập
        // eslint-disable-next-line no-undef
        await dispatch("fetchCart");
      } catch (error) {
        console.error("Facebook login failed:", error);
      }
    },
    logout({ commit }) {
      commit("LOGOUT");
      localStorage.removeItem("cart"); // Xóa giỏ hàng khỏi Local Storage khi người dùng đăng xuất
      window.location.href = "/"; // Chuyển về trang chủ sau khi logout
    },
    async fetchFurniture({ commit }) {
      try {
        const response = await axios.get("http://localhost:5000/api/furniture");
        commit("SET_FURNITURE", response.data);
      } catch (error) {
        console.error("Fetch furniture failed:", error);
      }
    },
    async fetchCart({ commit, state }) {
      if (state.user) {
        try {
          const response = await axios.get("http://localhost:5000/api/cart", {
            headers: { Authorization: `Bearer ${state.token}` },
          });
          commit("SET_CART", response.data); // Cập nhật giỏ hàng từ server và lưu vào Local Storage
        } catch (error) {
          console.error("Failed to fetch cart:", error);
        }
      }
    },

    async saveCart({ state }) {
      if (state.user && state.token) {
        // Chỉ lưu giỏ hàng lên server khi người dùng đã đăng nhập
        try {
          await axios.post(
            "http://localhost:5000/api/cart",
            {
              cart: state.cart,
            },
            {
              headers: { Authorization: `Bearer ${state.token}` },
            }
          );
        } catch (error) {
          console.error("Failed to save cart:", error);
        }
      }
    },
    addToCart({ commit, dispatch }, product) {
      const productToAdd = {
        _id: product._id,
        name: product.name,
        price: product.price,
        material: product.material, // Đảm bảo thuộc tính 'material' được thêm vào
        image: product.image,
        quantity: product.quantity,
      };
      commit("ADD_TO_CART", productToAdd);
      dispatch("saveCart"); // Lưu giỏ hàng sau khi thêm sản phẩm
    },

    removeFromCart({ commit, dispatch }, productId) {
      commit("REMOVE_FROM_CART", productId);
      dispatch("saveCart"); // Lưu giỏ hàng sau khi xóa sản phẩm
    },
  },
  getters: {
    isAuthenticated(state) {
      return !!state.token;
    },
    cartItems(state) {
      return state.cart || [];
    },
    cartItemCount(state) {
      return Array.isArray(state.cart)
        ? state.cart.reduce((total, item) => total + item.quantity, 0)
        : 0;
    },
  },
});
