<template>
  <div class="login-wrapper">
    <div class="login-box">
      <!-- Tiêu đề -->
      <h2 class="login-title">Login</h2>

      <!-- Form nhập Email -->
      <div class="input-group">
        <label for="email">Email Address</label>
        <div class="input-wrapper">
          <ion-icon name="mail-outline"></ion-icon>
          <input type="email" v-model="email" placeholder="Enter your email" required />
        </div>
      </div>

      <!-- Form nhập Password -->
      <div class="input-group">
        <label for="password">Password</label>
        <div class="input-wrapper">
          <ion-icon name="lock-closed-outline"></ion-icon>
          <input type="password" v-model="password" placeholder="Enter your password" required />
        </div>
        <!-- Thông báo lỗi khi tài khoản hoặc mật khẩu không chính xác -->
        <p v-if="loginError" class="error-message">{{ loginError }}</p>
      </div>

      <!-- Nút đăng nhập -->
      <button class="login-button" @click="login">Login</button>

      <!-- Đăng nhập bằng Facebook -->
      <div class="social-login">
        <span>Or login with</span>
        <button class="facebook-button" @click="loginWithFacebook">
          <ion-icon name="logo-facebook"></ion-icon>
          Login with Facebook
        </button>
      </div>

      <!-- Footer Links -->
      <div class="footer-links">
        <span @click="goToSignUp">Sign up</span>
        <span @click="forgotPassword">Forgot Password?</span>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      email: '',
      password: '',
      loginError: '', // Thêm biến này để lưu trữ lỗi đăng nhập
    };
  },
  methods: {
    async login() {
      try {
        // eslint-disable-next-line no-unused-vars
        const response = await this.$store.dispatch('login', {
          email: this.email,
          password: this.password
        });

        this.$router.push('/');
      } catch (error) {
        console.error('Login failed:', error);
        this.loginError = 'Tài khoản hoặc mật khẩu không chính xác'; // Hiển thị thông báo lỗi
      }
    },
    loginWithFacebook() {
      // Chuyển hướng người dùng tới route /auth/facebook trên backend để đăng nhập bằng Facebook
      window.location.href = 'http://localhost:5000/auth/facebook';
    },
    goToSignUp() {
      this.$router.push('/register');
    },
    forgotPassword() {
      // Chuyển hướng đến trang quên mật khẩu
    }
  }
};
</script>

