          Cart
          <span v-if="cartItemCount > 0" class="cart-count">{{ cartItemCount }}</span>
        </a>

        <!-- Hiển thị Manage Furniture cho admin hoặc employee -->
        <a v-if="user && (user.role === 'admin' || user.role === 'employee')" href="/manage-furniture" class="nav-item">Manage Furniture</a>
      </nav>

      <!-- Hiển thị tên người dùng hoặc nút đăng nhập/đăng ký -->
      <div v-if="isAuthenticated" class="auth-buttons">
        <span class="user-name">Welcome, {{ user.name }}!</span>
        <a @click="logout" class="logout-button">Logout</a>
      </div>
      <div v-else class="auth-buttons">
        <a href="/register" class="register-button">Register</a>
        <a href="/login" class="login-button">Login</a>
      </div>

      <!-- Nút mở menu di động -->
      <button class="menu-toggle" @click="toggleMobileMenu">
        <span class="menu-icon">&#9776;</span>
      </button>
    </div>

    <!-- Menu di động -->
    <div class="mobile-menu" v-if="mobileMenuOpen">
      <nav class="mobile-nav-links">
