        <a v-if="!isAuthenticated" href="/register" class="mobile-nav-item">Register</a>
        <a v-if="!isAuthenticated" href="/login" class="mobile-nav-item">Login</a>
        <a v-if="isAuthenticated" @click="logout" class="mobile-nav-item">Logout</a>
      </nav>
    </div>
  </header>
</template>

<script>
import { mapGetters, mapState } from 'vuex';
export default {
  name: 'NavbarComponent',
  computed: {
    ...mapState(['user']),
    ...mapGetters(['cartItemCount', 'isAuthenticated']) // Thêm isAuthenticated từ Vuex
  },
  methods: {
    toggleMobileMenu() {
      this.mobileMenuOpen = !this.mobileMenuOpen;
    },
    logout() {
      this.$store.dispatch('logout');
      this.$router.push('/');
    }
  },
  data() {
    return {
      mobileMenuOpen: false
    };
  }
};
</script>

