<template>
  <div class="manage-furniture-container">
    <!-- Header -->
    <header class="manage-header">
      <h1>Manage Furniture</h1>
      <button @click="openAddModal" class="add-product-button">+ Add New Product</button>
    </header>

    <!-- Bảng hiển thị sản phẩm -->
    <div class="product-list">
      <table class="product-table">
        <thead>
          <tr>
            <th>Image</th>
            <th>Name</th>
            <th>Description</th>
            <th>Price</th>
            <th>Category</th>
            <th>Stock Status</th>
            <th>Actions</th>
          </tr>
        </thead>
        <tbody>
          <tr v-for="product in furniture" :key="product._id">
            <td><img :src="`http://localhost:5000/uploads/${product.image}`" alt="Furniture Image" class="product-image"></td>
            <td>{{ product.name }}</td>
            <td>{{ product.description }}</td>
            <td>${{ product.price }}</td>
            <td>{{ product.category }}</td>
            <td>
              <span :class="product.inStock ? 'in-stock' : 'out-of-stock'">
                {{ product.inStock ? 'In Stock' : 'Out of Stock' }}
              </span>
            </td>
            <td>
              <button @click="openEditModal(product)" class="edit-button">Edit</button>
              <button @click="deleteProduct(product._id)" class="delete-button">Delete</button>
            </td>
          </tr>
        </tbody>
      </table>
    </div>

    <!-- Modal thêm/sửa sản phẩm -->
    <div v-if="showModal" class="modal">
      <div class="modal-content">
        <h2>{{ isEditing ? 'Edit Product' : 'Add New Product' }}</h2>
        <form @submit.prevent="isEditing ? updateProduct() : addProduct()">
          <div class="form-group">
            <label for="name">Name</label>
            <input type="text" v-model="formData.name" required>
          </div>
          <div class="form-group">
            <label for="description">Description</label>
            <textarea v-model="formData.description" required></textarea>
          </div>
          <div class="form-group">
            <label for="price">Price</label>
            <input type="number" v-model="formData.price" required>
          </div>
          <div class="form-group">
            <label for="category">Category</label>
            <input type="text" v-model="formData.category" required>
          </div>
          <div class="form-group">
            <label for="image">Image</label>
            <input type="file" @change="handleFileUpload">
          </div>
          <div class="form-group">
            <label for="inStock">In Stock</label>
            <input type="checkbox" v-model="formData.inStock">
          </div>
          <div class="modal-footer">
            <button type="submit" class="submit-button">{{ isEditing ? 'Update' : 'Add' }}</button>
            <button @click="closeModal" class="cancel-button">Cancel</button>
          </div>
        </form>
      </div>
    </div>
  </div>
</template>

<script>
import { mapState } from 'vuex';

export default {
  data() {
    return {
      showModal: false,
      isEditing: false,
      formData: {
        name: '',
        description: '',
        price: '',
        category: '',
        image: '',
        inStock: false,
      },
      editingProductId: null,
    };
  },
  computed: {
    ...mapState(['furniture', 'user']) // Lấy thông tin furniture và user từ Vuex store
  },
  async mounted() {
    if (!this.user) {
      // Nếu chưa có thông tin người dùng, tải lại từ token
      await this.$store.dispatch('loadUserFromToken');
    }

    // Fetch furniture sau khi tải thông tin người dùng
    this.$store.dispatch('fetchFurniture');
  },
  methods: {
    openAddModal() {
      this.isEditing = false;
      this.formData = {
        name: '',
        description: '',
        price: '',
        category: '',
        image: '',
        inStock: false,
      };
      this.showModal = true;
    },
    openEditModal(product) {
      this.isEditing = true;
      this.editingProductId = product._id;
      this.formData = { ...product };
      this.showModal = true;
    },
    closeModal() {
      this.showModal = false;
    },
    handleFileUpload(event) {
      const file = event.target.files[0];
      this.formData.image = file;
    },
    addProduct() {
      const formData = new FormData();
      for (let key in this.formData) {
        formData.append(key, this.formData[key]);
      }
      this.$store.dispatch('addProduct', formData);
      this.closeModal();
    },
    updateProduct() {
      const formData = new FormData();
      for (let key in this.formData) {
        formData.append(key, this.formData[key]);
      }
      this.$store.dispatch('updateProduct', { id: this.editingProductId, formData });
      this.closeModal();
    },
    deleteProduct(productId) {
      this.$store.dispatch('deleteProduct', productId);
    },
  },
};
</script>


